import React from "react";
import Errorimg from '../../my-app-new/src/images/error.svg';

const Error = () => {

  return (
    <>
      
      <div className="text-center mt-5 pt-5">
      <img  className=' error_img img-fluid' src={Errorimg} alt="" />
      </div>
      <h1 className='error-heading'>This Page Does Not Exist</h1>
    </>
  );
}

export default Error;
