import React from 'react'
import { useState } from 'react';
import contactImg from '../src/images/contact.svg';

 const Contact = () => {
     const [data, setdata] = useState([{
         fname:'',
         number:'',
         email:'',
         message:'',
     }]);

     const inputEvent = (event) => {
         const {name,value} = event.target;

         setdata((preval) => {
            return{
                ...preval,
                [name]: value,
            };
         });
     };

     const formSubmit = (e) => {
       e.preventDefault();
     }
    return(
        <>
            
            <section className="contactUs">
                <div className="cotainer-fluid">
                    <div className="row">
                    <h1 className='mt-5 mb-5 text-center'>Contact Us    </h1>
                        <div className="col-md-6">
                            <form onSubmit={formSubmit} action="">
                                <input type="text" placeholder="Full Name*" onChange={inputEvent} value={data.fname} name="fname" id="" />
                                <input type="number" placeholder="Your Number*" onChange={inputEvent} value={data.number} name="number" id="" />
                                <input type="email" placeholder="Your Email*" onChange={inputEvent} value={data.email} name="email" id="" />
                                <textarea name="" placeholder='Message*' onChange={inputEvent} value={data.message} name="message" id="" cols="30" rows="10"></textarea>
                                <button data-bs-toggle="modal" data-bs-target="#staticBackdrop" className='btn_common' type="submit">Submit</button>
                            </form>
                        </div>
                        <div className="col-md-5 offset-1">
                            <img className='img-fluid img-animated' src={contactImg} alt="" />
                        </div>
                    </div>
                </div>
            </section>

            <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="staticBackdropLabel">User Information</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <p>User Name : {data.fname}</p>
                    <p>User Mobile No : {data.number}</p>
                    <p>User Email : {data.email}</p>
                    <p>User Message : {data.message}</p>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                    <button type="button" class="btn btn-primary">Save</button>
                </div>
                </div>
            </div>
            </div>
        </>
    )
}

export default Contact;