import React from 'react'
import ReactDOM from 'react-dom'
import { NavLink } from 'react-router-dom'
import Banner from './Component/Banner'
import web from '../src/images/undraw_Push_notifications_re_t84m.svg'


const Home = () => {
    return(
        <>
            
           <Banner 
                title='Grow Your SMS Website With'
                path='/service'
                btnName='Get Started'
                imgSrc={web}
           />
        </>
    )
}

export default Home;