import React from 'react'
import ReactDOM from 'react-dom';
import { NavLink } from 'react-router-dom'



const Banner = (props) => {
    return(
        <>
            
            <section className='common_banner'>
                <div className="cotainer-fluid">
                    <div className="col-md-10 mx-auto">
                    <div className="row">
                        <div className="col-md-6">
                            <h1> {props.title} <br /> <strong className='TextHelight'>Kuldeep Singh</strong></h1>
                            <h5 className='mt-3 mb-2'>Lorem Ipsum is simply dummy text of the printing and typesetting industry.</h5>
                            <NavLink exact className='btn_common' to={props.path}>{props.btnName}</NavLink>
                        </div>
                        <div className="col-md-6">
                            <div className="banner_img">
                            <img className='img-fluid img-animated' src={props.imgSrc} alt="banner img" />
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Banner;